// Mengganti import 'next/image'
interface ImageProps {
    src: string;
    alt: string;
    fill?: boolean;
    className: string;
    width?: number;
    height?: number;
    priority?: boolean;
    quality?: number;
}

const Image: React.FC<ImageProps> = ({ src, alt, fill, className, width, height, priority }) => (
  <img 
    src={src} 
    alt={alt} 
    className={className} 
    style={{ 
      objectFit: fill ? 'cover' : undefined, 
      width: fill ? '100%' : (width ? `${width}px` : undefined), 
      height: fill ? '100%' : (height ? `${height}px` : (width ? `${width * 4 / 3}px` : undefined)), // Default aspect ratio for height
    }} 
    loading={priority ? 'eager' : 'lazy'}
    onError={(e: React.SyntheticEvent<HTMLImageElement, Event>) => {
        e.currentTarget.onerror = null; 
        e.currentTarget.src = 'https://placehold.co/300x400/4b5563/ffffff?text=Image+Error';
    }}
  />
);

// Mengganti import '@/components/ui/button' (shadcn/ui Button)
interface ButtonProps extends React.ButtonHTMLAttributes<HTMLButtonElement> {
    children: React.ReactNode;
    variant?: 'primary' | 'outline';
    className?: string;
}

const Button: React.FC<ButtonProps> = ({ children, variant = 'primary', className = '', disabled, onClick, ...props }) => {
  const baseClasses = "inline-flex items-center justify-center rounded-md text-sm font-medium transition-colors disabled:pointer-events-none disabled:opacity-50 h-10 px-4 py-2";
  
  let variantClasses = "bg-primary text-primary-foreground hover:bg-primary/90"; // Default (primary)
  if (variant === 'outline') {
    variantClasses = "border border-primary text-primary bg-background hover:bg-primary/15";
  }

  const finalClasses = `${baseClasses} ${variantClasses} ${className}`;

  return (
    <button 
      className={finalClasses} 
      disabled={disabled} 
      onClick={onClick}
      {...props}
    >
      {children}
    </button>
  );
};

// Mock Navbar
const Navbar: React.FC = () => (
    <nav className="p-4 bg-background/80 backdrop-blur-sm border-b border-border shadow-md fixed w-full top-0 left-0 z-50">
        <div className="max-w-6xl mx-auto text-xl font-bold text-foreground">Anime Detail Immersive</div>
    </nav>
);

