// Komponen Pembantu untuk menampilkan list relasi
const RelatedContent: React.FC<{ relations: Relation[] }> = ({ relations }) => {
  if (!relations || relations.length === 0) return null;

  return (
    <section className="px-4 sm:px-6 lg:px-8">
      <h2 className="text-2xl font-bold text-foreground mb-4">Related Content</h2>
      <div className="space-y-3">
        {relations.map((rel, index) => (
          <div key={index} className="flex flex-col sm:flex-row gap-1 sm:gap-4 p-3 bg-card rounded-lg border border-border items-start sm:items-center">
            <span className="font-semibold text-primary min-w-[120px]">{rel.relation}:</span>
            <div className="flex flex-wrap gap-2 pt-1 sm:pt-0">
              {rel.entry.map((entry) => (
                <a
                  key={entry.mal_id}
                  href={`#${entry.mal_id}`} // Menggunakan anchor karena tidak ada router
                  className="text-muted-foreground text-sm hover:text-foreground hover:underline transition-colors"
                  target="_blank" 
                  rel="noopener noreferrer"
                >
                  {entry.name}
                </a>
              ))}
            </div>
          </div>
        ))}
      </div>
    </section>
  );
};

