// Komponen pembantu untuk Stats Grid (DIKOREKSI TYPINGNYA)
interface StatCardProps {
    icon: React.ReactNode;
    label: string;
    value: string | number;
}
const StatCard: React.FC<StatCardProps> = ({ icon, label, value }) => (
    <div className="flex flex-col items-center md:items-start gap-1 bg-card p-3 rounded-lg border border-border shadow-sm text-center md:text-left">
        {icon}
        <span className="text-xs md:text-sm text-muted-foreground">{label}</span>
        <span className="text-base md:text-lg font-bold text-primary truncate w-full">{value}</span>
    </div>
);


export default function AnimePage({ params }: AnimePageProps) {
  // Menggunakan state untuk data
  const [anime, setAnime] = useState<Anime | null>(null);
  const [loading, setLoading] = useState<boolean>(true);
  const [showFullSynopsis, setShowFullSynopsis] = useState<boolean>(false); 

  // Pengganti notFound() dari Next.js (Logika pemeriksaan ID string menjadi number)
  const animeId = parseInt(params.id);
  if (isNaN(animeId)) return (
    <main className="min-h-screen bg-background flex items-center justify-center"><p className="text-foreground text-lg">404 Not Found</p></main>
  );

  useEffect(() => {
    async function fetchData() {
      try {
        // ID diabaikan karena menggunakan mock data
        const data = await getAnimeDetail(animeId); 
        setAnime(data);
      } catch (err) {
        console.error('Failed to fetch anime:', err);
      } finally {
        setLoading(false);
      }
    }
    fetchData();
  }, [animeId]);

  if (loading || !anime) {
    return (
      <main className="min-h-screen bg-background flex items-center justify-center pt-20">
        <Navbar />
        <div className="text-foreground text-lg mt-20 flex items-center gap-2">
            <svg className="animate-spin h-5 w-5 text-primary" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24"><circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle><path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path></svg>
            Fetching Anime Data...
        </div>
      </main>
    );
  }

  // --- PREPARASI DATA ---
  const imageUrl = anime.images?.jpg?.large_image_url || 'https://placehold.co/1280x720/374151/ffffff?text=Background+Placeholder'
  const titleEnglish = anime.title_english || 'N/A'
  const score = anime.score != null ? anime.score.toFixed(2) : 'N/A'
  const episodes = anime.episodes?.toString() ?? 'TBA'
  const source = anime.source || 'N/A'
  const year = anime.year?.toString() ?? 'TBA'
  const trailerUrl = anime.trailer?.url
  const genres = anime.genres ?? []
  const synopsis = anime.synopsis || 'No synopsis available.'
  const airedFrom = anime.aired?.from ? new Date(anime.aired.from).toLocaleDateString() : 'TBA'
  const airedTo = anime.aired?.to ? new Date(anime.aired.to).toLocaleDateString() : 'Ongoing'
  const relations = anime.relations ?? []
  const characters = anime.characters ?? []
  
  // Logika Collapsible Synopsis
  const MAX_SYNOPSIS_LENGTH = 500;
  const needsTruncation = synopsis.length > MAX_SYNOPSIS_LENGTH;
  const displayedSynopsis = needsTruncation && !showFullSynopsis 
    ? synopsis.substring(0, MAX_SYNOPSIS_LENGTH) + '...' 
    : synopsis;

  return (
    <main className="min-h-screen bg-background overflow-x-hidden text-foreground">

      <Navbar />

      {/* HERO BACKGROUND */}
      <div className="relative h-112 md:h-128 w-full overflow-hidden pt-[64px]"> 
        <Image
          src={imageUrl}
          alt="Anime background"
          fill
          className="object-cover blur-sm"
          quality={80}
          priority 
        />

        {/* Overlay gradient untuk kontras teks */}
        <div className="absolute inset-0 bg-gradient-to-t 
          from-background via-background/20 to-transparent">
        </div>
      </div>

      {/* CONTENT WRAPPER */}
      <div className="relative z-20 max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 -mt-24 md:-mt-40 grid grid-cols-1 md:grid-cols-3 gap-8">

        {/* POSTER (MD: COL 1) */}
        <div className="relative w-44 md:w-60 mx-auto md:mx-0">
          <div className="shadow-2xl rounded-xl overflow-hidden group transition-transform duration-500 hover:scale-105">
            <Image
              src={'https://placehold.co/300x400/374151/ffffff?text=Poster'} 
              alt={anime.title}
              width={300}
              height={400}
              className="w-full h-auto"
              priority
            />
            <div className="absolute inset-0 bg-black/10 group-hover:bg-black/20 transition-all"></div>
          </div>
        </div>

        {/* RIGHT TEXT SECTION (MD: COL 2 & 3) */}
        <div className="md:col-span-2 text-center md:text-left flex flex-col justify-center gap-4">
          <h1 className="text-3xl md:text-5xl font-extrabold text-white drop-shadow-lg leading-tight">
            {anime.title}
          </h1>
          <p className="text-white/80 text-lg drop-shadow-sm font-semibold">{titleEnglish}</p>

          {/* STATS GRID (6 item - Responsive 2/3/6 layout) */}
          <div className="grid grid-cols-2 sm:grid-cols-3 xl:grid-cols-6 gap-3 mt-4">

            {/* 1. Rating */}
            <StatCard icon={<Star className="text-yellow-400" />} label="Score" value={score} />
            
            {/* 2. Episodes */}
            <StatCard icon={<Film className="text-blue-500" />} label="Episodes" value={episodes} />

            {/* 3. Source */}
            <StatCard icon={<BookOpen className="text-red-500" />} label="Source" value={source} />

            {/* 4. Year */}
            <StatCard icon={<Calendar className="text-purple-500" />} label="Year" value={year} />

            {/* 5. Popularity (BARU) */}
            <StatCard icon={<TrendingUp className="text-indigo-400" />} label="Popularity Rank" value={anime.popularity != null ? `#${formatNumber(anime.popularity)}` : 'N/A'} />

            {/* 6. Members (BARU) */}
            <StatCard icon={<Users className="text-pink-500" />} label="Members" value={formatNumber(anime.members)} />
          </div>

          {/* ACTION BUTTONS */}
          <div className="flex flex-wrap gap-3 mt-4 justify-center md:justify-start">

            {/* Trailer Button */}
            {trailerUrl ? (
              <Button
                className="gap-2 bg-primary text-white hover:bg-primary/90"
                onClick={() => window.open(trailerUrl, '_blank')}
              >
                <Play size={18} /> Watch Trailer
              </Button>
            ) : (
              <Button disabled className="gap-2 bg-gray-500/50 text-white cursor-not-allowed">
                <Play size={18} /> No Trailer
              </Button>
            )}

            {/* Favorites - Kontras state aktif diperbaiki */}
            <Button
              variant="outline"
              className="gap-2 border-primary text-primary 
                         hover:bg-primary/15 
                         active:bg-primary/25 active:text-primary 
                         focus-visible:ring-2 focus-visible:ring-primary focus-visible:ring-offset-2 focus-visible:ring-offset-background
                         transition-colors"
            >
              <Heart size={18} /> Add to Favorites
            </Button>

            {/* Share - Kontras state aktif diperbaiki */}
            <Button
              variant="outline"
              className="gap-2 border-primary text-primary 
                         hover:bg-primary/15 
                         active:bg-primary/25 active:text-primary 
                         focus-visible:ring-2 focus-visible:ring-primary focus-visible:ring-offset-2 focus-visible:ring-offset-background
                         transition-colors"
            >
              <Share2 size={18} /> Share
            </Button>
          </div>
        </div>
      </div>

      {/* --- BAGIAN KONTEN DI BAWAH GRID --- */}
      <div className="relative max-w-6xl mx-auto mt-8 md:mt-12 space-y-12 pb-16">

        {/* 1. GENRES */}
        {genres.length > 0 && (
          <div className="px-4 sm:px-6 lg:px-8 flex flex-wrap gap-3">
            {genres.map((genre) => (
              <span
                key={genre.mal_id}
                className="px-3 py-1 bg-card border border-primary text-primary text-sm rounded-full transition-colors hover:bg-primary/10"
              >
                {genre.name}
              </span>
            ))}
          </div>
        )}

        {/* 2. SYNOPSIS (Dengan Collapsible - BARU) */}
        <section className="px-4 sm:px-6 lg:px-8">
          <h2 className="text-2xl font-bold text-foreground mb-4">Synopsis</h2>
          <p className="text-muted-foreground leading-relaxed whitespace-pre-line transition-all duration-500">
            {displayedSynopsis}
          </p>
          {needsTruncation && (
            <Button 
              onClick={() => setShowFullSynopsis(!showFullSynopsis)}
              className="mt-2 p-0 h-auto text-primary hover:text-primary/80 bg-transparent hover:bg-transparent"
              variant="primary" // Memastikan menggunakan variant yang valid
            >
              {showFullSynopsis ? 'Read Less' : 'Read More'}
            </Button>
          )}
        </section>

        {/* 3. AIRING STATUS & DATES */}
        <section className="px-4 sm:px-6 lg:px-8">
          <h2 className="text-2xl font-bold text-foreground mb-4">Airing Details</h2>
          <div className="grid grid-cols-1 sm:grid-cols-3 gap-4 text-sm text-muted-foreground bg-card p-4 rounded-lg border border-border">
            <p className='flex items-center gap-2'><Activity size={16} className='text-green-500'/> <strong>Status:</strong> <span className='font-semibold text-foreground'>{anime.status}</span></p>
            <p className='flex items-center gap-2'><Calendar size={16} className='text-gray-400'/> <strong>Aired From:</strong> <span className='font-semibold text-foreground'>{airedFrom}</span></p>
            <p className='flex items-center gap-2'><Calendar size={16} className='text-gray-400'/> <strong>Aired To:</strong> <span className='font-semibold text-foreground'>{airedTo}</span></p>
          </div>
        </section>
        
        {/* 4. PRODUCTION & STAFF */}
        <ProductionDetails studios={anime.studios} staff={anime.staff} />

        {/* 5. CHARACTER LIST (Ditambahkan kembali) */}
        {characters.length > 0 && <CharacterList characters={characters} />}

        {/* 6. RELATED CONTENT / ADAPTATIONS */}
        {relations.length > 0 && <RelatedContent relations={relations} />}

      </div>
    </main>
  )
}